import React, { useState, useRef, useCallback } from "react";
import { QrReader } from "react-qr-reader";
import axios from "axios";
import { useLocation } from "react-router-dom";

export const Check = () => {
  const location = useLocation();
  const initialSelectedProducts = location.state?.selectedProducts || []; // Optional chaining 
  const [selectedProducts, setSelectedProducts] = useState(
    initialSelectedProducts
  ); // useState 
  const [userId, setUserId] = useState(null);
  const isQRCodeScanned = useRef(false);

  const handleScanQrCode = useCallback((result, error) => {
    if (!!result && !isQRCodeScanned.current) {
      isQRCodeScanned.current = true;
      setUserId(result.text);
      console.log(result.text);
      handlePayment();
      setSelectedProducts([]);
      console.log(selectedProducts);
    }
  });

  const handleError = (err) => {
    console.error(err);
  };

  const handlePayment = async () => {
    try {
      await Promise.all(
        selectedProducts.map((product) =>
          axios.post("http://127.0.0.1:8080/check", {
            user_id: userId,
            merchandise: product.merchandise,
          })
        )
      );
      //  (: )
    } catch (error) {
      console.error(":", error);
      //  (: )
    }
  };

  return (
    <div>
      <div style={{ width: "50%", margin: "0 auto" }}>
        <QrReader
          delay={100}
          onError={handleError}
          onResult={handleScanQrCode}
          style={{ width: "100%" }}
        />
      </div>
      <h2></h2> {/*  */}
      <ul>
        {selectedProducts.map((product, index) => (
          <li key={index}>{product.merchandise}</li>
        ))}
      </ul>
    </div>
  );
};
